	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2037806786\" title=\"View 'CIMG0337' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2385/2037806786_df61018b21.jpg\" alt=\"CIMG0337\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Somewhere behind the big red trash container, the cement mixer and the scaffolding is the main entrance to our restaurant. Yes, we're still open! And we're still running double digit increases in our guest count over last year! Here are some <a href=\"http://www.flickr.com/photos/papascott/sets/72157603204608056/\">more photos</a> of the construction.</p>\n<p>In the meantime we've put our own branding on the container...</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2037053629\" title=\"View 'CIMG0549' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2389/2037053629_b44c0992c6.jpg\" alt=\"CIMG0549\" border=\"0\" width=\"500\" height=\"375\" /></a></p>","fields":{"title":"Find The Entrance","date":"16 Nov 2007","url":"https://papascott.de/2007-11-16-find-the-entrance/"}}},"pathContext":{"slug":"/2007-11-16-find-the-entrance/","prev":{"fields":{"title":"The Subjunctive Mood is a Sign of Weakness","date":"12 Nov 2007","slug":"/2007-11-12-the-subjunctive-mood-is-a-sign-of-weakness/"}},"next":{"fields":{"title":"Is \"Ami\" an Insult?","date":"16 Nov 2007","slug":"/2007-11-16-is-ami-an-insult/"}}}}

